html {
    background-color: white;
}
body {
    margin: 0;
    font-size: 13px;
    font-family:
        -apple-system,
        BlinkMacSystemFont,
        "Segoe UI",
        Roboto,
        Oxygen,
        Ubuntu,
        Cantarell,
        "Fira Sans",
        "Droid Sans",
        "Helvetica Neue",
        sans-serif;
    color: rgb(46 46 46);
    background-color: white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
code {
    font-family:
        source-code-pro,
        Menlo,
        Monaco,
        Consolas,
        "Courier New",
        monospace;
}

@font-face {
    font-family: dacing-script;
    src: url(./assets/font/dacing-script.ttf);
}
a {
    font-weight: 500;
    color: white;
}
ul {
    margin: 0;
    padding: 0;

    /* list-style-type: none; */
}
ul li {
    margin: 0;
    padding: 0;
}
