.ng-content {
    position: fixed;
    top: 0;
    z-index: 10;
    width: 100%;
    height: 64px;
    background: #ffffffcc;

    /* box-shadow: inset 0 -1px 0 0 rgb(0 0 0 / 10%); */
    transition: background 0.36s;
    transition-delay: 0.3s;
    backdrop-filter: blur(10px);
}
.ng-content-white {
    background-color: white;
}
.ng-pc {
    display: flex;
    justify-content: center;
    height: 100%;
    flex-direction: row;
}
.ng-pc-choice-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 1200px;
}
.ng-pc-choice-right-content {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}
.ng-pc-choice {
    display: flex;
    justify-content: center;
    width: 100px;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
    color: #666666;
    transition: 0.2s;
    line-height: 64px;
    cursor: pointer;
    flex-direction: row;
}
.ng-pc-choice:hover {
    color: #000000;
}
.ng-choice-content {
    display: flex;
    flex-direction: row;
}
.np-choice-icon-content {
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.ng-choice-icon {
    margin-left: 4px;
    width: 16px;
}
.github-content {
    display: flex;
    justify-content: center;
    width: 60px;
    flex-direction: column;
}
.github-middle-content {
    display: flex;
    justify-content: center;
    flex-direction: row;
}
.icon {
    width: 20px;
    height: 20px;
}
.ng-mobile {
    display: none;
    height: 100%;
}
.ng-mobile-text {
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
    flex-direction: row;
}
.ng-mobile-placeholder {
    width: 40px;
}
.ng-mobile-dropdown {
    display: none;
    height: 100vh;
    background-color: white;
}
.ng-mobile-dropdown-show {
    display: block;
}
.ng-mogile-dropdown-text-content {
    padding: 0 45px;
}
.ng-mobile-dropdown-text {
    width: 100%;
    height: 44px;
    font-size: 15px;
    font-weight: 500;
    border-bottom: 1px solid rgb(0 0 0 / 10%);
    color: rgb(0 0 0 / 79%);
    line-height: 44px;
}

@media screen and (max-width: 910px) {
    .ng-pc {
        display: none;
    }
    .ng-mobile {
        display: block;
    }
}

@media screen and (min-width: 911px) and (max-width: 1199px) {
    .ng-pc-choice-content {
        width: 800px;
    }
}

@media screen and (min-width: 1200px) and (max-width: 1792px) {
    .ng-pc-choice-content {
        width: 1000px;
    }
}

@media screen and (min-width: 1793px) {
    .ng-pc-choice-content {
        width: 1200px;
    }
}
